html {
  --header-color: #00acdc;
}

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  display: block;
}

.logo__img {
  width: 100%;
  height: 40px;
  display: block;
}

.nav__list {
  text-transform: uppercase;
  height: 60px;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
  padding: 0;
}

.nav__item:last-child {
  margin: 0;
}

.nav__link {
  color: #000;
  line-height: 60px;
  text-decoration: none;
  transition: color .5s, border-color .5s;
  display: block;
}

.nav__link:hover {
  color: var(--header-color);
  border-bottom: 4px solid;
}

a.is-active {
  color: var(--header-color);
  position: relative;
}

a.is-active:hover {
  border-bottom: 4px solid #0000;
}

.is-active:after {
  content: "";
  background-color: var(--header-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:nth-child(4) {
  color: var(--header-color);
}

.nav__link:last-child {
  margin: 0;
  padding: 0;
}
